:root {
  --accent-color: #02885b;
  --text-color: #363535;
  --main-font: "Lato", sans-serif;
  --background-color: #fbfbfa;
  --update-card-background: #fdfdfd;
  --span-color: #04ce7a;
  --accent-text-color: #bcbcbc;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.accordian {
  width: 650px;
}

#big-box {
  padding: 50px;
}

body {
  font-size: 100%;
  margin: 0;
  font-family: "Maison Neue Book", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  color: #282828;
}

.bt-container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 80px;
  gap: 10px;
  padding: 10px;
}

.btn {
  background-color: #fff;
  padding: 10px 30px;
  border: 1px solid lightgrey;
  margin-bottom: 30px;
  cursor: pointer;
}

.story-container {
  grid-area: story;
}
.creator-container {
  grid-area: creator;
}
.support-container {
  grid-area: support;
}

.campaign-container {
  display: grid;
  grid-template-columns: 68% 20%;
  grid-template-areas:
    "story creator"
    "story support";
  gap: 10px;
  padding: 10px;
}

.campaign-container > div {
  text-align: center;
  padding: 20px 0;
}

