    "story support";
  gap: 10px;
  padding: 10px;
}

.campaign-container > div {
  text-align: center;
  padding: 20px 0;
}

.btn:hover {
  border: 1px solid #000;
  box-shadow: 1px 1px 4px 1px lightgrey;
}

.card-container {
  display: grid;
  grid-template-rows: 80px;
  padding: 10px;
}

.card-container > div {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.card-footer {
  position: relative;
  z-index: 999;
  box-shadow: -3px -3px 90px 90px #fff;
  overflow-x: hidden;
  margin-top: 30px;
}

.comments-likes {
  display: flex;
  flex-direction: row;
  color: var(--accent-text-color);
  font-size: 1rem;
  position: relative;
  z-index: 1000;
  gap: 20px;
}

.creator span {
  background-color: var(--span-color);
  font-size: 0.8rem;
  color: white;
  border-radius: 2px;
  padding: 2px 5px;
}

.date {
  color: var(--accent-text-color);
  font-size: 0.8rem;
}

.faq-answer,
.faq-date {
  color: black;
}

.faq-answer.show {
  display: block;
  height: auto;
  max-height: 9999px;
  transition: all 0.5 cubic-bezier(0, 1, 0, 1);
}

.faq-question {
  font-weight: 500;
  color: #282828;
  font-size: 1.4rem;
  line-height: 2.4rem;
  box-sizing: inherit;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  font: inherit;
}

.faq-header {
  margin-bottom: 0rem;
  font-weight: 400;
  display: inline-block;
  font-size: 2.4rem;
  line-height: 3rem;
  box-sizing: inherit;
}

.faq-text {
  font-weight: 500;
  color: #282828;
  font-size: 1.4rem;
  line-height: 2.4rem;
  box-sizing: inherit;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  font: inherit;
}
/*-----------------------------*/
.greeting {
  margin: 20px 0;
}

.hide {
  display: none;
}

.item {
  background: lightgray;
  margin-bottom: 5px;
  padding: 10px 20px;
}

.menu {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  gap: 10px;
  padding: 10px;
}

.menu > div {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.name-date {
  border-bottom: 1px solid lightgrey;
  padding-bottom: 10px;
}

.new-comment-btn {
  background-color: #5555ff;
  color: white;
  --bttn-icon: white;
  border: #5555ff;
  --bttn-hover-bg: #0a007d;
  --bttn-hover-text: white;
  --bttn-hover-icon: white;
  --bttn-hover-border: #0a007d;
}

.faq-link {
  color: #282828;
  text-decoration: underline;
  font-weight: 700;
}

.list-item {
  /* outline: 2px dashed black; */
  list-style: none;
  padding: 1.2em;
  border-color: #e8e8e8;
  border: 0.1em solid #dcdedd;
  background-color: #fbfbfa;
}

.log {
  font-weight: bold;
  color: #282828;
  font-size: 14px;
}

.border-box {
  /* outline: 2px solid black; */
  background-color: #ffffff;
  border-color: #e8e8e8;
  border: 0.1em solid #dcdedd;
}

.comment-list {
  padding: 1.2em;
}

.col-left {
  width: 66.6666666667%;
  float: left;
}

.row-comments {
  margin-right: -1.8em;
  margin-left: -1.8em;
}

.col-right {
  width: 33.333333333%;
  float: right;
  padding-left: 1.8rem;
  padding-right: 1.8rem;
  box-sizing: border-box;
}
/*======================COMMENTS CSS=================================*/

.pledge-container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: 80px 80px;
  gap: 10px;
  padding: 10px;
}

.pledge-container > span {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.wrapper {
  display: flex;
  height: 100vh;
  width: 100vh;
  justify-content: center;
  align-content: center;
}

.summary {
  margin-bottom: 10px;
}

.update-container {
  margin: 0 auto;
  padding: 30px;
  max-width: 816px;
}

.update-card:hover {
  border: 1px solid #000;
  transition: 0.8s ease-in-out;
  overflow: hidden;
}

.update-section {
  background-color: var(--background-color);
}

.p1:hover {
  padding-bottom: 30px;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}

.update-card {
  background: var(--update-card-background);
  border: 1px solid lightgrey;
  padding: 40px;
  margin-bottom: 2rem;
  overflow: hidden;
}

.update-number {
  margin: 10px 0;
  color: var(--accent-text-color);
  text-transform: uppercase;
  font-size: 0.8rem;
}

.update-flex {
  display: flex;
  justify-content: space-between;
}

.community {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  border-bottom: 1px solid lightgrey;
}

.community-supporters {
  font-size: 1.4rem;
}

.community-headings {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

